.SidebarOption {
    display: flex;
    align-items: center;
    cursor: pointer;
}


.SidebarOption:hover {
    background-color: #e8f5fe;
    border-radius: 30px;
    color: var(--twitter-color);
    transition: color 100ms ease-out;
}


.SidebarOption > .MuiSvgIcon-root {
     padding: 20px;
}

.SidebarOption > h2 {
    font-weight: 800;
    font-size: 20px;
    margin-right: 20px;
}

.SidebarOption--active {
    color: var(--twitter-color);
}

